<template>
  <div class="page3">
    <!-- eslint-disable -->
    <img
      src="@/assets/img/main-logo.png"
      class="img-fluid top-img mx-auto mt-3"
    />
    <h4 class="text-white text-center font-weight-bolder">
      #CovidYogaChallenge
    </h4>
    <h1
      class="text-white text-center text-uppercase font-weight-bolder font-space"
    >
      STARTS IN
    </h1>
    <div class="mx-auto">
      <RadialProgress
        :completed-steps="completedSteps"
        innerStrokeColor="#fff"
        startColor="#23A49F"
        stopColor="#23A49F"
        :animateSpeed="1000"
        :strokeWidth="10"
        :totalSteps="30"
        :diameter="320"
        :fps="60"
      >
        <h1 class="center-text text-white font-weight-bolder">
          {{ completedSteps }}
        </h1>
      </RadialProgress>
      <div class="bottom-footer">
        <i
          class="ti-timer"
          :style="{ color: !completedSteps ? 'white' : '#23a49f' }"
        />
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  data: () => ({
    countDown: null,
    completedSteps: 30,
  }),
  methods: {
    myTimer() {
      this.countDown = setInterval(() => {
        this.completedSteps--;
        if (this.completedSteps === 0) {
          console.log("TIMER_COMPLETED");
          clearInterval(this.countDown);
          this.$router.push("/page-4");
        }
      }, 1000);
    },
  },
  created() {
    this.myTimer();
  },
  destroyed() {
    clearInterval(this.countDown);
  },
};
</script>

